.testimonials {
   padding: 40px 0 73px;
   &__title {
      margin-bottom: 52px;
      @include xs {
         margin-bottom: 30px; } }
   &__slider {
      @include md {
         padding: 0 15px; }
      @include sm {
         max-width: 738px;
         margin: 0 auto; }
      .slick-list {
         margin: 0 -15px; }
      .slider-btn {
         top: calc(50% - 40px);
         @include xs {
            top: 45px; }
         &_prev {
            left: -50px;
            @include md {
               left: -15px; }
            @include xs {
               left: 0; } }
         &_next {
            right: -50px;
            @include md {
               right: -5px; }
            @include xs {
               right: 0; } } } }
   &__person {
      $parent: &;
      padding: 0 15px;
      &-inner {
         display: flex;
         @include xs {
            flex-direction: column; } }
      &-text {
         position: relative;
         flex: 1;
         padding: 15px 0 20px 34px;
         @include md {
            padding: 15px 0 20px 20px; }
         @include xs {
            padding: 0 0 20px; } }
      &-data {
         flex: 0 0 135px;
         text-align: center;
         @include xs {
            margin-bottom: 20px; } }
      &-photo {
         margin-bottom: 10px;
         overflow: hidden;
         width: 135px;
         height: 135px;
         border-radius: 50%;
         @include cover-img;
         @include xs {
            margin: 0 auto 10px; }
         &_top img {
            object-position: top; } }

      &-name {
         @include f('pn700', 18, 22); }
      &-city {
         font-size: 16px; }
      &-pos {
         margin-bottom: 13px;
         @include f('pn700', 22, 24); }
      &-speech {
         position: relative;
         max-height: 160px;
         padding-left: 26px;
         @include f('pn400i', 16, 24);
         i {
            position: absolute;
            top: 15px;
            left: -6px;
            font-size: 60px;
            color: $color2; } } } }
