.nav-btn {
  display: none;
  position: relative;
  top: -11px;
  border: 0;
  outline: 0;
  padding: 0;
  z-index: 11;
}

@media only screen and (max-width: 1023px) {
  .nav-btn {
    display: block;
  }
}

.nav-btn.is-active span {
  color: #212121;
}

.nav-btn__burger {
  display: block;
  margin: auto;
  width: 34px;
  height: 34px;
  cursor: pointer;
}

.nav-btn__lines {
  display: block;
  width: 34px;
  height: 3px;
  position: relative;
  transform: translateY(25px);
  background-color: #fff;
  transition: all .3s;
}

.nav-btn.is-active .nav-btn__lines {
  background: rgba(255, 255, 255, 0);
}

.nav-btn__lines:after, .nav-btn__lines:before {
  width: 34px;
  height: 3px;
}

.nav-btn__lines:before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 10px;
  background-color: #fff;
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav-btn__lines:after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  background-color: #fff;
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav-btn.is-active .nav-btn__lines:after {
  top: 0;
  transform: rotate(45deg);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

.nav-btn.is-active .nav-btn__lines:before {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}

